@import "../../sass/variables.scss";
@import "../../sass/functions.scss";
@import "../../sass/fonts.scss";
@import "./breakpoints.scss";

.hero {
  display: inline-block;
  
  background-color: $secondary-color;
  height: 900px;
  width: 100vw;
  clip-path: polygon(0px 0px, 100% 0px, 100% 85%, 0px 100%);
  &__image {
    margin: 0 auto;
      border: 1px solid $primary-color;
      overflow: hidden;
      border-radius: 15px;
      width: 330px;
      height: 470px;
      box-shadow: 0px 20px 33px 0px rgba(0, 0, 0, 0.7);
      position: relative;
      top: -30px;
      margin-top: 50px;

  
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
  }
  &__description {
    height: 100%;
    margin-top: 15px;
    padding: 10px;
    h1 {
      color: $primary-color;
      font-size: 28px;
    }
    p {
      color: grey;
      font-size: 20px;
      letter-spacing: 1px;
    }
  }

  @include media(tablet) {
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    &__description {
      h1 {
        font-size: 45px;
      }
      p {
        font-size: 30px;
      }
    }
  }
  @include media(tablet) {
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    &__description {
      h1 {
        font-size: 45px;
      }
      p {
        font-size: 30px;
      }
    }
  }
  @include media(desktop-s) {
    display: flex;
    justify-content: center;
    align-items: center;
    &__description {
      margin-left: 60px;
      margin-right: 60px;
      h1 {
        font-size: 65px;
        margin-top: 150px;

      }
      p {
        font-size: 40px;
      }
    }
  }
}
