@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,300&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{background-color:#f0f0f0;font-family:"Lato",sans-serif}


.navbar{display:flex;justify-content:space-around;align-items:center;height:160px;background-color:#12254d;flex-wrap:wrap;width:100vw;padding:10px}.navbar--img{display:flex;justify-content:center;align-items:center;width:100px;height:88px;border:6px solid white;border-radius:10px;margin-bottom:10px}.navbar__list-container ul{display:flex;flex-wrap:wrap;justify-content:space-around;width:450px;list-style:none;font-size:25px}.navbar__list-container ul li a{color:#f0f0f0;text-decoration:none;transition:0.3s}.navbar__list-container ul li a:hover{color:rgba(240,240,240,0.7);text-decoration:underline}

.hero{display:inline-block;background-color:#12254d;height:900px;width:100vw;-webkit-clip-path:polygon(0px 0px, 100% 0px, 100% 85%, 0px 100%);clip-path:polygon(0px 0px, 100% 0px, 100% 85%, 0px 100%)}.hero__image{margin:0 auto;border:1px solid #f0f0f0;overflow:hidden;border-radius:15px;width:330px;height:470px;box-shadow:0px 20px 33px 0px rgba(0,0,0,0.7);position:relative;top:-30px;margin-top:50px}.hero__image img{width:100%;height:100%;object-fit:cover}.hero__description{height:100%;margin-top:15px;padding:10px}.hero__description h1{color:#f0f0f0;font-size:28px}.hero__description p{color:grey;font-size:20px;letter-spacing:1px}@media only screen and (min-width: 768px){.hero{padding:20px;display:flex;justify-content:center;align-items:center}.hero__description h1{font-size:45px}.hero__description p{font-size:30px}}@media only screen and (min-width: 768px){.hero{padding:20px;display:flex;justify-content:center;align-items:center}.hero__description h1{font-size:45px}.hero__description p{font-size:30px}}@media only screen and (min-width: 1024px){.hero{display:flex;justify-content:center;align-items:center}.hero__description{margin-left:60px;margin-right:60px}.hero__description h1{font-size:65px;margin-top:150px}.hero__description p{font-size:40px}}

.wrapper{color:#12254d;margin-top:10px;padding-left:220px}.contact-section{display:flex;flex-wrap:wrap;justify-content:space-evenly;width:100vw;margin-top:20px}.contact-section .contact__description{margin-bottom:70px;display:flex;flex-direction:column;justify-content:space-evenly}.contact-section .contact__description h1{font-size:37px;color:#12254d;margin:0 auto}.contact-section .contact__description ul{display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;width:400px;height:270px;grid-gap:3px}.contact-section .contact__description ul li{display:flex;justify-content:center;align-items:center;border-radius:8px;list-style:none;transition:0.3s}.contact-section .contact__description ul li img{width:60px;transition:0.3s}.contact-section .contact__description ul li .express{width:100px}.contact-section .contact__description ul li:hover{font-size:22px;box-shadow:0px 0px 20px 0px rgba(18,37,77,0.719)}.contact-section .contact__description ul li:hover img{width:76px}.contact-section .contact__description ul li:hover .express{width:110px}.contact-section .contact__description-social{margin-bottom:70px;display:flex;flex-direction:column;justify-content:space-evenly}.contact-section .contact__description-social h1{font-size:37px;color:#12254d;margin:0 auto}.contact-section .contact__description-social ul{display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;width:400px;height:270px;grid-gap:3px}.contact-section .contact__description-social ul li{display:flex;justify-content:center;align-items:center;border-radius:8px;list-style:none;transition:0.3s}.contact-section .contact__description-social ul li img{width:60px;transition:0.3s}.contact-section .contact__description-social ul li .social-icon{color:black}.contact-section .contact__description-social ul li:hover{font-size:22px;box-shadow:0px 0px 20px 0px rgba(18,37,77,0.719)}@media only screen and (min-width: 1024px){.contact-section{flex-wrap:nowrap}.contact-section .contact__description{margin-left:20px}.contact-section .contact__description ul{grid-template-columns:1fr 1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr;width:570px;height:190px;grid-gap:7px}.contact-section .contact__container{width:420px}.contact-section .contact__container .container__body input{width:350px;height:50px}}

