@import "../../sass/variables.scss";
@import "../../sass/functions.scss";
@import "../../sass/fonts.scss";
@import "./breakpoints.scss";

.wrapper {
  color: $secondary-color;
  margin-top: 10px;
  padding-left: 220px;
}

.contact-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100vw;
  margin-top: 20px;
  .contact__description {
    margin-bottom: 70px;
    display: flex;

    flex-direction: column;
    justify-content: space-evenly;
    h1 {
      font-size: 37px;
      color: $secondary-color;
      margin: 0 auto;
    }
    ul {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr ;
      grid-template-rows: 1fr 1fr 1fr;
      width: 400px;
      height: 270px;
      grid-gap: 3px;
      
      li {
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 8px;
        list-style: none;
        transition: 0.3s;
        img {
          width: 60px;
          transition: 0.3s;
        }
        .express {
          width: 100px;
        }
      }
      li:hover {
        font-size: 22px;
        box-shadow: 0px 0px 20px 0px rgba(18, 37, 77, 0.719);
        img {
          width: 76px;
        }
        .express {
          width: 110px;
        }
      }
    }
  }
  .contact__description-social {
    margin-bottom: 70px;
    display: flex;

    flex-direction: column;
    justify-content: space-evenly;
    h1 {
      font-size: 37px;
      color: $secondary-color;
      margin: 0 auto;
    }
    ul {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr ;
      grid-template-rows: 1fr 1fr 1fr;
      width: 400px;
      height: 270px;
      grid-gap: 3px;
      
      li {
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 8px;
        list-style: none;
        transition: 0.3s;
        img {
          width: 60px;
          transition: 0.3s;
        }
        
        .social-icon {
          color: black;
        }
      }
      li:hover {
        font-size: 22px;
        box-shadow: 0px 0px 20px 0px rgba(18, 37, 77, 0.719);
        
        
      }
    }
  }
  
  @include media(desktop-s) {
    flex-wrap: nowrap;
    .contact__description {
      margin-left: 20px;
      ul {
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr ;
        grid-template-rows: 1fr 1fr;
        width: 570px;
        height: 190px;
        grid-gap: 7px;
      }
    }
    .contact__container {
      width: 420px;
      .container__body {
        input {
          width: 350px;
          height: 50px;
        }
      }
    }
  }
}
