@import "../../sass/variables.scss";
@import "../../sass/functions.scss";
.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 160px;
  background-color: $secondary-color;
  flex-wrap: wrap;
  width: 100vw;
  padding: 10px;

  &--img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 88px;
    border: 6px solid white;
    border-radius: 10px;
    margin-bottom: 10px;
  }
  &__list-container {
    ul {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      width: 450px;
      list-style: none;
      font-size: 25px;
      li {
        a {
          color: $primary-color;
          text-decoration: none;
          transition: 0.3s;
        }
        a:hover {
          color: get-opacity($primary-color, 0.7);
          text-decoration: underline;
        }
      }
    }
  }
}
